@import '../../utils/variable.scss';
@import '../../utils/normalize.scss';
@import '../../utils/mixins.scss';


.card {
  box-sizing: border-box;
  max-width: 272px;
  border-radius: 8px;
  border: 1px solid $Elements;
  background: $White;
  padding: 32px;
  line-height: normal;
  font-style: normal;

  &__img {
    display: block;
    margin: 0 auto;
    max-width: 208px;
    max-height: 196px;
    cursor: pointer;
  }

  &__title {
    height: 72px;
    color: $Primary;
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    padding-top: 24px;
    padding-bottom: 8px;
    cursor: pointer;

    @include hover(color, $Accent);
  }

  &__price {
    display: flex;
    gap: 8px;
    margin-bottom: 8px;

    &-actual{
      color: $Primary;
      font-size: 22px;
      font-weight: 800;
      font-style: normal;
      line-height: 140%;
    }

    &-sale {
      color: $Secondary;
      font-size: 22px;
      font-weight: 500;
      text-decoration: line-through;
      font-style: normal;
      line-height: normal;
    }
  }

  &__description {
    padding-top: 1px;
    margin-bottom: 17px;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    gap: 8px;
    padding: 0;
  }

  @include hover(box-shadow, scale(1.2));

  &:hover {
    border-color: $Icons;
    box-shadow: 0 8px 15px #41414129;
  }
}

.description-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 8px;

  &-title {
    color:  $Secondary;
    font-size: 12px;
    font-weight: 600;
  }

  &-value {
    color: $Primary;
    text-align: right;
    font-size: 12px;
    font-weight: 700;
  }
}

hr {
  border-radius: 8px;
  border: 1px solid $Elements;
  background: $White;
}
